.tk-alibaba-sans { font-family: "alibaba-sans",sans-serif; }

body {
  margin: 0;
  font-family: "alibaba-sans",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  font-weight: 500;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body iframe {
  display: none;
  height: 0;
  width: 0;
}

::-webkit-scrollbar {
  width: 5px;
  border-radius: 2px;
}
 
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
::-webkit-scrollbar-thumb {
  border-radius: 2px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), inset 0px 1px 2px #FFFFFF, inset 0px 20px 40px rgba(255, 255, 255, 0.25), inset 0px -4px 4px #2a2b2a, inset 0px -40px 40px rgba(9, 10, 31, 0.2), inset 0px 4px 12px rgba(255, 255, 255, 0.4);
}

body, html, #root {
  min-height: 100% !important;
    height: 100%;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.btn_loader {
  position: absolute;
}

.zig_btn {
  box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.01);
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.35);
}

.zig_btn:hover {
  box-shadow: 0 0 1px 2px rgba(255, 255, 255, 0.3) inset,
    0 0 8px 1px rgba(120, 255, 255, 0.4);
}

.zig_btn:active {
  transition: none;
  background-color: #087fcf;
  box-shadow: -2px -2px 4px 1px rgba(0, 0, 0, 0.2) inset,
    0 1px 0 1px rgba(255, 255, 255, 0.2) inset;
}

.bg_btn {
  display: flex;
  align-items: center;
  transition: all 0.1s linear;
  height: 35px;
  padding: 4px 30px;
  font-weight: bold;
  background: linear-gradient(93.46deg, #2AABEE 16.94%, #0CCFCF 97.24%);
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.08), 0px 3px 3px -1px rgba(0, 0, 0, 0.08);
  border: 0;
  border-radius: 8px;
  width: 100%;
  justify-content: center;
  position: relative;
  color: white;
  font-size: 14px;
  line-height: 1.5;
  outline: none;
}

.zig_disabled {
  opacity: 0.6;
  pointer-events: none;
}

.card-container:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: #26b8e9 0px 0 20px 2px !important;
  transition: all 0.3s ease-out;
}

.card-view-container {
  -webkit-transform: scale(1);
          transform: scale(1);
  box-shadow: #26b8e9 0px 0 10px 0px !important;
  transition: all 0.3s ease-in  !important;
}

