.tk-alibaba-sans { font-family: "alibaba-sans",sans-serif; }

body {
  margin: 0;
  font-family: "alibaba-sans",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  font-weight: 500;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body iframe {
  display: none;
  height: 0;
  width: 0;
}

::-webkit-scrollbar {
  width: 5px;
  border-radius: 2px;
}
 
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
 
::-webkit-scrollbar-thumb {
  border-radius: 2px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), inset 0px 1px 2px #FFFFFF, inset 0px 20px 40px rgba(255, 255, 255, 0.25), inset 0px -4px 4px #2a2b2a, inset 0px -40px 40px rgba(9, 10, 31, 0.2), inset 0px 4px 12px rgba(255, 255, 255, 0.4);
}

body, html, #root {
  min-height: 100% !important;
    height: 100%;
}